<template>
  <div style="height: 100%;">
    <div class="login-header">
      <div style="margin: auto 20px auto 20px;height: 10%;padding: 20px 2px;">
        <img class="login-ico-style" src="../../assets/img/loginIco.jpg">
        <span class="project-font-style" style="margin-left: 10px;font-size: 35px;line-height:100%;vertical-align:middle;">MIA</span>
      </div>
    </div>
    <div id="login-body">
      <el-form ref="form" :model="form" :rules="rules2" class="demo-ruleForm">
        <div style="font-size: 50px;margin-left: 20px;margin-top: 45px;">登录</div>
        <div style="margin-top: 36px;margin-left: 26px;">
          <div >
            <div style="margin-bottom: 10px;">账号</div>
            <el-form-item prop="loginId"
                          :rules="[
                        { required: true, message: '请输入账号!'}
                      ]">
              <el-input style="width: 80%;" v-model="form.loginId" placeholder="请输入邮箱">
                <template slot="prepend"><i class="el-icon-s-custom"></i></template>
              </el-input>
            </el-form-item>
          </div>

          <div style="margin-top: 26px;">
            <div style="margin-bottom: 10px;">密码</div>
            <el-form-item prop="loginPwd"
                          :rules="[
                        { required: true, message: '请输入密码!'}
                      ]">
              <el-input style="width: 80%;" type="password" v-model="form.loginPwd" placeholder="">
                <template slot="prepend"><i class="el-icon-lock"></i></template>
              </el-input>
            </el-form-item>
          </div>

          <div style="margin-top: 26px;">
            <el-button type="primary" @click="goLogin">登录</el-button>
            <div style="margin-top: 10px;font-size: 13px;">
              <a href="">无法访问你的账户?</a>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<a href="">忘记密码?</a>
            </div>
          </div>

          <div style="margin-top: 23px;font-size: 16px;">
            <span>没有 MIA 账户?</span>&nbsp;&nbsp;&nbsp;<el-link type="primary" @click="goRegister">立刻注册</el-link>
          </div>

        </div>
      </el-form>
    </div>
    <div class="login-footer">
      <div style="margin-top: 39px;text-align: center;">
        <a href="">隐私和Cookie</a> | <a href="">条款</a>
      </div>
      <div style="margin-top: 10px;text-align: center;">
        <span>©2019 MIA</span>
      </div>
    </div>
  </div>
</template>

<script>
  import {post} from 'api/post'

  export default {
    name: "login",
    data() {
      return {
        rules2: {},
        form: {
          loginId: '',
          loginPwd: ''
        }
      }
    },
    create() {

    },
    methods: {
      goLogin: function() {
        let expireDays = 1000 * 60 *60

        this.$refs['form'].validate((valid) => {
          if(valid) {
            post('login/verify', {loginId: this.form.loginId, loginPwd: this.form.loginPwd}).then((res) => {
              console.log(res)
              if(res.retCode === this.GLOBAL.SUCCESS) {
                if(res.result.valid === 'Y') {
                  this.$cookie.delete('authorization')
                  this.$cookie.set('authorization', res.result.userOid, expireDays)

                  this.$cookie.delete("userInfo")
                  this.$cookie.set('userInfo', JSON.stringify(res.result.userProfile), expireDays)

                  this.$router.push('/ui/layout/home')
                } else {
                  this.$message.error(res.retMsg)
                }
              } else {
                this.$message.error(res.retMsg)
              }
            })
          }
        })
      },
      goRegister: function() {
        this.$router.push('/ui/layout2/register')
      }
    }
  }
</script>

<style scoped>
  .login-header {
    height: 12%;
    background-color: #d2d6da;
    box-shadow: 0 1px 5px rgba(0,0,0,0.3)
  }

  .login-footer {
    font-size: 15px;
  }

  .login-ico-style {
    width: 10%;
    line-height: 100%;
    vertical-align: middle;
  }

  .project-font-style{
    background: -webkit-gradient(linear,left top,left bottom,from(#FF0052),to(#802F7B));
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    text-shadow:none !important;
  }
  .project-font-style :after {
    background: none;
    content: attr(data-text);
    left: 0;
    position: absolute;
    text-shadow: 4px 4px 0px #441369, 8px 8px 0px rgba(255,255,255,0.1); //relief shade effect
  top: 0;
    z-index: -1;
  }

</style>



