<template>
    <div>
      <div style="margin-top: 30px;">
        <div style="width: 86%;background-color: rgb(241, 208, 159);margin: 0 auto; padding: 10px;">
          <div style="text-align: center;font-size: 90px;">
            <i class="el-icon-s-custom"></i>
          </div>
          <div style="margin-top: 0;text-align: center;">
            <span style="font-size: 15px;">{{userName}}</span>&nbsp;&nbsp;
            <span style="font-size: 11px;">Lv{{userLevel}}</span><br>
            <span style="font-size: 11px;">{{userEmail}}</span>
          </div>
        </div>
      </div>
      <div id="myprofile-body" style="margin-top: 20px;">
        <el-divider></el-divider>
        <div style="margin-left: 16px;">
          <i class="el-icon-postcard" style="font-size: 20px;line-height:100%;vertical-align:middle;"></i>&nbsp;&nbsp;&nbsp;&nbsp;
          <span style="line-height:100%;vertical-align:middle;">个人认证</span>
          <i class="el-icon-arrow-right" style="line-height:100%;vertical-align:middle;position: absolute;right: 25px;"></i>
        </div>
        <el-divider></el-divider>
        <div style="margin-left: 16px;">
          <i class="el-icon-chat-dot-round" style="font-size: 20px;line-height:100%;vertical-align:middle;"></i>&nbsp;&nbsp;&nbsp;&nbsp;
          <span style="line-height:100%;vertical-align:middle;">消息通知</span>
          <i class="el-icon-arrow-right" style="line-height:100%;vertical-align:middle;position: absolute;right: 25px;"></i>
        </div>
        <el-divider></el-divider>
        <div style="margin-left: 16px;">
          <i class="el-icon-help" style="font-size: 20px;line-height:100%;vertical-align:middle;"></i>&nbsp;&nbsp;&nbsp;&nbsp;
          <span style="line-height:100%;vertical-align:middle;">帮助</span>
          <i class="el-icon-arrow-right" style="line-height:100%;vertical-align:middle;position: absolute;right: 25px;"></i>
        </div>
        <el-divider></el-divider>
      </div>
      <div>
        <div style="margin-top: 50px;text-align:center">
          <el-button style="" type="primary" icon="el-icon-sort">切换账号</el-button>
          <el-button style="" type="danger" icon="el-icon-switch-button" @click="logout">退出登录</el-button>
        </div>
      </div>
    </div>
</template>

<script>
    export default {
        name: "myProfile",
        data() {
          return {
            userName: '会飞的鱼',
            userEmail: '190212392@qq.com',
            userLevel: 1,
            userInfo: {}
          }
        },
      created() {
        console.log('myProfile created!')
        this.userInfo = JSON.parse(this.$cookie.get('userInfo'))
        console.log(this.userInfo)
        this.userName = this.userInfo.userName
        this.userEmail = this.userInfo.email
        this.userLevel = this.userInfo.level
      },
      methods: {
        logout: function () {
          this.$router.push('/ui/login')
        }
      }
    }
</script>

<style scoped>
  #myprofile-body .el-divider--horizontal {
    margin: 15px 0;
  }

</style>



