import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/view/login/login'
import Layout from '@/view/base/layout'
import Layout2 from '@/view/base/layout2'
import Home from  '@/view/home'
import Setting from  '@/view/setting/setting'
import MyProfile from '@/view/myProfile/myProfile'
import CreateProject from '@/view/project/createProject'
import CreateProjectSuccess from '@/view/project/createProjectSuccess'
import ProjectList from '@/view/project/projectList'
import ProjectItem from '@/view/project/projectItem'
import AnalysisRes from '@/view/project/analysisRes'
import Register from  '@/view/login/register'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/ui/login',
      name: 'login',
      component: Login,
    },
    {
      path: '/ui/layout',
      name: 'layout',
      component: Layout,
      children: [
        {
          path: 'home',
          name: 'home',
          component: Home
        },
        {
          path: 'setting',
          name: 'setting',
          component: Setting
        },
        {
          path: 'myprofile',
          name: 'myprofile',
          component: MyProfile
        }
      ]
    },
    {
      path: '/ui/layout2',
      name: 'layout2',
      component: Layout2,
      children: [
        {
          path: 'createproject',
          name: 'createproject',
          component: CreateProject
        },
        {
          path: 'createprojsuccess',
          name: 'createprojsuccess',
          component: CreateProjectSuccess
        },
        {
          path: 'projectlist',
          name: 'projectlist',
          component: ProjectList
        },
        {
          path: 'projectitem',
          name: 'projectitem',
          component: ProjectItem
        },
        {
          path: 'analysisres',
          name: 'analysisres',
          component: AnalysisRes
        },
        {
          path: 'register',
          name: 'register',
          component: Register
        }
      ]
    }
  ]
})



