<template>
    <div style="height: 100%;">
      <div id="create-project-header" style="margin-top: 15px;">
        <el-page-header style="margin-left: 6px;" @back="goBack" content="创建工程">
        </el-page-header>
        <el-divider></el-divider>
      </div>
      <div>
        <!--<div style="display: block;height: 50px;">
          <div class="button-area">
            <el-button size="mini" type="primary">保存工程</el-button>
          </div>
        </div>-->

        <el-form ref="form" :model="form" :rules="rules2" class="demo-ruleForm">
          <div style="margin-left: 26px;">
            <el-form-item prop="projectName">
              <template slot="label">
                <span>工程名称</span>
                <span>:</span>
              </template>
              <el-input size="mini" v-model="form.projectName" maxlength="250" style="margin-left: 10%;width: 50%;"></el-input>
            </el-form-item>
            <el-form-item>
              <template slot="label">
                <span style="margin-left: 9px;">工程描述</span>
                <span>:</span>
              </template>
              <el-input size="mini" type="textarea" v-model="form.projectDesc" maxlength="50" style="margin-left: 10%;width: 50%;"></el-input>
            </el-form-item>
            <el-form-item prop="projectName"
                          :rules="[
                    { required: true, message: '请输入工程名!'}
                  ]">
              <template slot="label">
                <span>工程图片</span>
                <span>:</span>
              </template>
              <el-upload
                class="upload-demo"
                ref="upload"
                :before-upload="beforeUpload"
                :auto-upload="false"
                :action="uploadUrl()"
                :file-list="fileList"
                list-type="picture"
                :on-success="successUpload"
                :limit="uploadLimit"
                :data="uploadData"
                multiple>
                <el-button size="mini" type="primary">点击上传</el-button>
                <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过2个文件</div>
              </el-upload>
            </el-form-item>
          </div>
         <div style="margin-top: 30px;">
           <el-button style="display:block;margin: 10px auto;" type="primary" @click="saveProject">保存工程</el-button>
         </div>
        </el-form>
      </div>
    </div>
</template>

<script>
  import {post} from 'api/post'

    export default {
      name: "createProject",
      data() {
        var verifyProjectName = (rule, value, callback) => {
          if (!value) {
            callback(new Error('请输入工程名称!'))
          } else{
            post('project/verify/name', {projectName: value}).then((res) => {
              console.log(res)
              if(res.retCode === this.GLOBAL.SUCCESS) {
                if(res.result.isExist === 'Y') {
                  callback(new Error('工程已存在!'))
                  return
                } else {
                  callback()
                  return
                }
              } else {
                callback(new Error(res.retMsg))
              }
            })
          }
        }

        return {
          userOid: '',
          form: {
            projectOid: '',
            userOid: '',
            projectName: '',
            projectDesc: '',
            fileCount: 0
          },
          fileList: [],
          uploadLimit: 2,
          uploadData: {
            defMark: '',
            newFileName: '',
            projectOid: ''
          },
          rules2: {
            projectName:[
              { required: true, message: '请输入工程名!'},
              {validator: verifyProjectName, trigger: 'blur'}
            ]
          }
        }
      },
      created() {
        let userInfo = JSON.parse(this.$cookie.get('userInfo'))
        this.userOid = userInfo.userOid
        this.form.userOid = userInfo.userOid
        this.uploadData.defMark = userInfo.userOid
        this.initSetting()
      },
      methods: {
        uploadUrl: function() {
          console.log(this.baseUrl)
          let url = this.baseUrl + 'api/project/create/upload'
          console.log('----- 上传图片URL -----' + url)
          return url
        },
        beforeUpload: function(file) {
          console.log('file', file)
          let splitArr = file.name.split('.')
          let suffix = splitArr[splitArr.length-1].trim()
          this.uploadData.newFileName = 'GP_' + file.uid + '.' + suffix
        },
        successUpload: function(response, file, fileList) {
          console.log("successUpload!!!!!")
          let flag = true
          if(response.retCode === this.GLOBAL.SUCCESS) {

          }

          for(let i = 0; i< this.$refs.upload.uploadFiles.length; i++) {
            if(this.$refs.upload.uploadFiles[i].status !== 'success') {
              flag = false
            }
          }

          if(flag) {
            if(response.retCode === this.GLOBAL.SUCCESS) {
              console.log(fileList)
              let filenameList = []
              for(let i = 0; i<fileList.length; i++) {
                filenameList[i] = fileList[i].name
              }
              sessionStorage.removeItem('fileList')
              sessionStorage.setItem('fileList', JSON.stringify({fileList: filenameList}))
              // this.$router.push('/ui/layout2/createprojsuccess')
              // this.$router.push({ name: 'createprojsuccess', params: { proOid: this.form.projectOid}})
              console.log('proOid: ' + this.form.projectOid)
              this.$router.push({ path: '/ui/layout2/createprojsuccess', query: {proOid: this.form.projectOid, proName: this.form.projectName} })
            } else {
              this.$message.error(response.retMsg)
            }
          }
        },
        goBack: function() {
          this.$router.push('/ui/layout/home')
        },
        initSetting:function() {
          post('project/create/init', {userOid: this.userOid}).then((res) => {
            console.log(res)
            if(res.retCode === this.GLOBAL.SUCCESS) {
              this.uploadLimit = res.result.maxLimitUpload
            } else {
              this.$message.error(res.retMsg)
            }
          })
        },
        saveProject: function() {
          if(this.$refs.upload.uploadFiles.length === 0) {
            this.$alert('你没有上传文件!')
            return
          }

          if(this.$refs.upload.uploadFiles.length > 2) {
            this.$alert('目前您最多只能上传2个文件!')
            return
          }
          if(this.$refs.upload.uploadFiles.length <= 2) {
            const  fileType = ['jpg', 'png', 'JPG', 'JPEG']
            for(let i = 0; i<this.$refs.upload.uploadFiles.length; i++) {
              let _arr = this.$refs.upload.uploadFiles[i].name.split('.')
              let key = _arr[_arr.length - 1]
              if(fileType.indexOf(key) === -1){
                this.$alert('仅支持上传特定格式(jpg、png、JPG、JPEG)的图片!')
                return
              }
            }
          }

          console.log('Start save Project!!!')
          this.$refs['form'].validate((valid) => {
            if (valid) {
              this.form.fileCount = this.$refs.upload.uploadFiles.length
              post('project/create/save', this.form).then((res) => {
                console.log(res)
                if(res.retCode === this.GLOBAL.SUCCESS) {
                  this.form.projectOid = res.result.projectOid
                  this.uploadData.projectOid =  res.result.projectOid
                  this.$refs.upload.submit()
                } else {
                  this.$message.error(res.retMsg)
                }
              })
            }
          })

        }
      }
    }
</script>

<style scoped>
  #create-project-header .el-divider--horizontal {
    margin: 15px 0;
  }

  .button-area {
    float: right;
    margin-right: 10px;
  }
</style>



