<template>
  <div class="common-footer" style="width: 100%;">
    <el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect"
             background-color="#545c64" text-color="#fff"
             active-text-color="#ffd04b" :router="router">
      <el-menu-item class="menu-item-width" index="home">
        <i class="el-icon-house"></i>
        <span>首页</span>
      </el-menu-item>
      <el-menu-item class="menu-item-width" index="setting">
        <i class="el-icon-setting"></i>
        <span>设置</span>
      </el-menu-item>
      <el-menu-item class="menu-item-width" index="myprofile">
        <i class="el-icon-user"></i>
        <span>我的</span>
      </el-menu-item>
    </el-menu>
  </div>
</template>

<script>
    export default {
        name: "commonFooter",
        data() {
          return {
            activeIndex: 'home',
            router:true
          }
        },
        watch:{
          $route(to,from){
            console.log(to.path);

          }
        },
        created() {
          console.log(this.$route.path.substring(this.$route.path.lastIndexOf('/')+1))
          this.activeIndex = this.$route.path.substring(this.$route.path.lastIndexOf('/')+1)

        },
        methods: {
          handleSelect: function () {
            
          }
        }
    }
</script>

<style scoped>
  .common-footer {
    position: fixed;
    bottom: 0;
  }

  .menu-item-width{
    width: 33%;
    text-align: center;
    font-size: 20px;
  }
</style>



