<template>
    <div>
      <div style="width: 99%;height: 20%;margin: 0 auto;">
          <el-carousel height="190px">
            <el-carousel-item style="height: 100%;" v-for="item in imgList" :key="item">
              <h3 class="small" style="margin:0;height: 100%;">
                <!--<img style="height: 100%;" :src="item">-->
                <el-image
                  style="width: 100%;"
                  :src="item" fit="contain "></el-image>
              </h3>
            </el-carousel-item>
          </el-carousel>
      </div>
      <div style="width: 100%;height: 80%;">
        <div style="margin: 50px 10px;padding: 20px;">
          <el-row :gutter="20" style="height: 90px;" >
            <el-col :span="10">
              <div class="grid-content bg-purple" style="text-align: center;" @click="createProject">
                <i class="el-icon-upload" style="font-size: 60px;line-height: 60px;vertical-align:middle"></i>
                <br><span>创建工程</span>
              </div>
            </el-col>
            <el-col :span="10" style="margin-left: 30px;">
              <div class="grid-content bg-purple" style="text-align: center;" @click="goProjectList">
                <i class="el-icon-s-grid" style="font-size: 60px;line-height: 60px;vertical-align:middle"></i>
                <br><span>工程记录</span>
              </div>
            </el-col>
          </el-row>
          <el-row :gutter="20" style="height: 90px;margin-top: 50px;" >
            <el-col :span="10">
              <div class="grid-content bg-purple" style="text-align: center;" @click="goDownload">
                <i class="el-icon-download" style="font-size: 60px;line-height: 60px;vertical-align:middle"></i>
                <br><span>下载</span>
              </div>
            </el-col>
            <el-col :span="10" style="margin-left: 30px;">
              <div class="grid-content bg-purple" style="text-align: center;">
                <i class="el-icon-more" style="font-size: 60px;line-height: 60px;vertical-align:middle"></i>
                <br><span>待续...</span>
              </div>
            </el-col>
          </el-row>
        </div>
      </div>
    </div>
</template>

<script>
    import carousel1 from '../assets/img/carousel1.jpg'
    import carousel2 from '../assets/img/carousel2.png'
    import carousel3 from '../assets/img/carousel3.jpg'

    export default {
      name: "home",
      data() {
        return {
          imgList: [carousel3, carousel2, carousel1]
        }
      },
      created() {
      },
      methods: {
        createProject: function() {
          console.log(111)
          this.$router.push('/ui/layout2/createproject')
        },
        goProjectList: function() {
          this.$router.push('/ui/layout2/projectlist')
        },
        goDownload: function () {

        }
      }
    }
</script>

<style scoped>
  .home-row-col {
    border:1px solid #3a8ee6;
  }
</style>



