<template>
    <div>
      <div id="pro-res-project-header" style="margin-top: 15px;">
        <span style="float: right;margin-right: 10px;color: #3a8ee6;font-weight: bold;" @click="goHome">完成</span>
        <el-page-header style="margin-left: 6px;" @back="goBack" content="工程分析">
        </el-page-header>
        <el-divider></el-divider>
      </div>
      <div style="margin-top: 26px;">
        <div style="width: 90%;margin: 0 auto;">
          <span class="common-label">工程名称</span>
          <span>:</span>
          <span style="margin-left: 10px;">{{form.projectName}}</span>
        </div>
        <div style="margin-top: 20px;">
          <div style="margin-left: 20px;margin-bottom: 10px;"><span class="common-label">波形图</span> <span>:</span></div>
          <div style="text-align: center;">
            <el-image
              style="width: 80%;"
              :src="testUrl"
              fit="contain"></el-image>
          </div>
        </div>
        <div style="margin-top: 20px;">
          <div style="margin-bottom: 10px;margin-left: 20px;">
            <span class="common-label">分析结果</span>
            <span>:</span>
          </div>
          <el-table
            :header-cell-style="{background: '#54a5f5', color: '#fff'}"
            :data="projectDataList"
            size="mini"
            style="width: 90%;margin: 0 auto;">
            <el-table-column
             align="center"
              label="No."
             type="index"
              width="40">
            </el-table-column>
            <el-table-column
              prop="diffX"
              label="X差值"
              width="65"
              align="center">
            </el-table-column>
            <el-table-column
              prop="diffY"
              label="Y差值"
              width="65"
              align="center">
            </el-table-column>
            <el-table-column
              prop="sensitivity"
              label="灵敏度"
              width="76"
              align="center">
            </el-table-column>
            <el-table-column
              prop="fom"
              label="品质因素"
              align="center">
            </el-table-column>
          </el-table>
        </div>
      </div>
    </div>
</template>

<script>
  import testUrl from '../../assets/img/carousel2.png'

    export default {
      name: "analysisRes",
      data() {
        return {
          testUrl: '',
          searchForm: {
            projectOid: ''
          },
          form: {
            projectName: 'T-PN1001',
            waveformUrl: testUrl
          },
          projectDataList: [
            {
              diffX: '30',
              diffY: '20',
              sensitivity: '2.01',
              fom: '16.10'
            }
          ]
        }
      },
      created() {
        if(!this.$route.query.proOid) {
          this.$router.push('/ui/layout2/projectlist')
        }

        this.searchForm.projectOid = this.$route.query.proOid

        this.testUrl = this.form.waveformUrl
      },
      methods: {
        goBack: function () {
          window.history.back(-1)
        },
        goHome: function () {
          this.$router.push('/ui/layout/home')
        }
      }
    }
</script>

<style scoped>
  #pro-res-project-header .el-divider--horizontal {
    margin: 15px 0;
  }
</style>



