<template>
    <div>
      <div id="create-res-project-header" style="margin-top: 15px;">
        <el-page-header style="margin-left: 6px;" title="主页" @back="goBack" content="创建成功">
        </el-page-header>
        <el-divider></el-divider>
      </div>
      <div>
        <div style="width: 95%;height: 15px;background-color: #2c3e50;margin: 0 auto;border-radius:10px;"></div>
        <el-card class="box-card" style="width: 85%;margin: 0 auto;height: 280px; position: relative;top: -7px;" shadow="never">
          <div style="text-align: center;">
            <i class="el-icon-circle-check" style="font-size: 36px;color: #5daf34;font-weight: bold;line-height: 40px;vertical-align:middle"></i>
            <span style="font-size: 25px;margin-left: 20px;font-weight: bold;line-height: 40px;vertical-align:middle">创建成功</span>
          </div>
          <div style="margin-top: 20px;font-size: 15px;">
            <div>
              <span>工程名称</span>
              <span>:</span>
              <span style="margin-left: 10px;">{{form.projectName}}</span>
            </div>

            <div>
              <span style="line-height: 40px;vertical-align: hanging;">工程文件</span>
              <span>:</span>
              <div v-for="file in form.fileList" style="margin-left: 20%;margin-bottom: 5px;">
                <span>{{file}}</span>
              </div>
            </div>
          </div>
          <div style="text-align: center;margin-top: 30px;">
            <el-button style="font-weight: bold;font-size: 20px;" type="primary" round @click="goAnalysis">前往分析</el-button>
          </div>
        </el-card>
      </div>
    </div>
</template>

<script>
    export default {
      name: "createProjectSuccess",
      data() {
        return {
          form: {
            projectOid: '1001',
            projectName: 'PN_TEST2019',
            fileList: ['test201901.jpg', 'test201902.jpg']
          }
        }
      },
      created() {
        console.log('create!')
        this.form.projectOid = this.$route.query.proOid
        this.form.projectName = this.$route.query.proName
        // this.form.fileList = this.$route.params.fileList
        this.form.fileList = JSON.parse(sessionStorage.getItem('fileList')).fileList
        console.log(this.form.projectOid)
        console.log(this.form.fileList)
      },
      methods: {
        goBack: function () {
          this.$router.push('/ui/layout/home')
        },
        goAnalysis: function () {
          this.$router.push({ path: '/ui/layout2/projectitem', query: {proOid: this.form.projectOid} })
        }
      }

    }
</script>

<style scoped>
  #create-res-project-header .el-divider--horizontal {
    margin: 15px 0;
  }
</style>



