yle=\"margin-bottom: 10px;\">账号</div>\n            <el-form-item prop=\"loginId\"\n                          :rules=\"[\n                        { required: true, message: '请输入账号!'}\n                      ]\">\n              <el-input style=\"width: 80%;\" v-model=\"form.loginId\" placeholder=\"请输入邮箱\">\n                <template slot=\"prepend\"><i class=\"el-icon-s-custom\"></i></template>\n              </el-input>\n            </el-form-item>\n          </div>\n\n          <div style=\"margin-top: 26px;\">\n            <div style=\"margin-bottom: 10px;\">密码</div>\n            <el-form-item prop=\"loginPwd\"\n                          :rules=\"[\n                        { required: true, message: '请输入密码!'}\n                      ]\">\n              <el-input style=\"width: 80%;\" type=\"password\" v-model=\"form.loginPwd\" placeholder=\"\">\n                <template slot=\"prepend\"><i class=\"el-icon-lock\"></i></template>\n              </el-input>\n            </el-form-item>\n          </div>\n\n          <div style=\"margin-top: 26px;\">\n            <el-button type=\"primary\" @click=\"goLogin\">登录</el-button>\n            <div style=\"margin-top: 10px;font-size: 13px;\">\n              <a href=\"\">无法访问你的账户?</a>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<a href=\"\">忘记密码?</a>\n            </div>\n          </div>\n\n          <div style=\"margin-top: 23px;font-size: 16px;\">\n            <span>没有 MIA 账户?</span>&nbsp;&nbsp;&nbsp;<el-link type=\"primary\" @click=\"goRegister\">立刻注册</el-link>\n          </div>\n\n        </div>\n      </el-form>\n    </div>\n    <div class=\"login-footer\">\n      <div style=\"margin-top: 39px;text-align: center;\">\n        <a href=\"\">隐私和Cookie</a> | <a href=\"\">条款</a>\n      </div>\n      <div style=\"margin-top: 10px;text-align: center;\">\n        <span>©2019 MIA</span>\n      </div>\n    </div>\n  </div>\n</template>\n\n<script>\n  import {post} from 'api/post'\n\n  export default {\n    name: \"login\",\n    da