<template>
  <el-container style="height: 100%;width: 100%;display: block;">
    <Header></Header>
    <el-main style="margin: 0;padding: 0;overflow-x: hidden;">
      <transition mode="out-in">
        <router-view></router-view>
      </transition>
    </el-main>
    <Footer></Footer>
  </el-container>

</template>

<script>
  import Header from '@/components/header/header'
  import Footer from '@/components/footer/footer'

  export default {
    name: 'layout',
    components: {
      Header,
      Footer
    }
  }

</script>

<style scoped>

</style>



