<template>
    <el-header style="height: 73px;margin: 0;padding: 0;">
      <div class="common-header">
        <div style="margin: auto 20px auto 20px;height: 100%;padding: 20px 2px;">
          <img class="common-ico-style" src="../../assets/img/loginIco.jpg">
          <span class="project-font-style" style="margin-left: 10px;font-size: 35px;line-height:100%;vertical-align:middle;">MIA</span>
        </div>
      </div>
    </el-header>
</template>

<script>
    export default {
        name: "commonHeader"
    }
</script>

<style scoped>
  .common-header {
    height: 100%;
    background-color: #d2d6da;
    box-shadow: 0 1px 5px rgba(0,0,0,0.3)
  }

  .common-ico-style {
    width: 10%;
    line-height: 100%;
    vertical-align: middle;
  }

  .project-font-style{
    background: -webkit-gradient(linear,left top,left bottom,from(#FF0052),to(#802F7B));
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    text-shadow:none !important;
  }
  .project-font-style :after {
    background: none;
    content: attr(data-text);
    left: 0;
    position: absolute;
    text-shadow: 4px 4px 0px #441369, 8px 8px 0px rgba(255,255,255,0.1); //relief shade effect
  top: 0;
    z-index: -1;
  }
</style>



