<template>
    <div>
      <div id="pro-list-project-header" style="margin-top: 15px;">
        <el-page-header style="margin-left: 6px;" @back="goBack" content="工程记录">
        </el-page-header>
        <el-divider></el-divider>
      </div>
      <div>
        <div style="text-align: right;">
          <el-button type="primary" size="mini" style="margin-right: 20px;" icon="el-icon-search" @click="searchProjectList">搜索</el-button>
        </div>
        <div id="search-area" style="width: 90%;margin: 0 auto;">
          <span>工程名称</span>
          <span>:</span>
          <el-input class="common-text-input" style="margin-left: 10px;" v-model="searchForm.projectName" placeholder="" size="mini" ></el-input>
        </div>
        <div>
          <div style="margin-left: 20px;margin-top: 20px;" class="">
            <span>工程列表</span>
          </div>
          <div id="pro-card-list" style="width: 90%;margin: 10px auto;">
            <el-card class="box-card pro-list-card-item" body-style="padding: 10px;" v-for="item in projectList">
              <div @click="goProjectItem(item)">
                <div style="">
                  <span style="font-size: 11px">工程名称</span>
                  <span style="font-size: 11px">:</span>
                  <span style="font-size: 11px;margin-left: 10px;font-weight: bold;">{{item.projectName}}</span>
                </div>
                <div style="margin-top: 10px;font-size: 10px;color: #A8A8A8;min-height: 10px;">
                  <p>{{item.projectDesc?item.projectDesc: '没有描述信息!'}}</p>
                </div>
                <div style="margin-top: 10px;font-size: 10px">
                  <span><i class="el-icon-time"></i></span>
                  <span>{{item.createDate}}</span>
                  <span style="margin-left: 20px;color: #2c3e50;"><i class="el-icon-tickets"></i>文件个数</span><span>:</span>
                  <span style="color: #A8A8A8;">{{item.fileCount}}</span>
                  <span style="float: right;color: #3a8ee6;cursor: pointer;">查看详情</span>
                </div>
              </div>
            </el-card>
          </div>
        </div>
      </div>
    </div>
</template>

<script>
  import {post} from 'api/post'

    export default {
      name: "projectList",
      data() {
        return {
          searchForm: {
            userOid: '',
            projectName: ''
          },
          activeNames: ['1'],
          projectList: [
            {
              projectOid: '1001',
              projectName: 'T-PN1001',
              projectDesc: 'It is the test1 Project !',
              fileCount: 2,
              createDate: '2019-02-12'
            },
            {
              projectOid: '1002',
              projectName: 'Test-1002',
              projectDesc: 'It is the test2 Project ! It Create for test file pro-2019-20.jpg.',
              fileCount: 1,
              createDate: '2019-05-21'
            },
            {
              projectOid: '1003',
              projectName: 'ProN-Test1003',
              projectDesc: '',
              fileCount: 2,
              createDate: '2019-02-12'
            }
          ]
        }
      },
      created() {
        let userInfo = JSON.parse(this.$cookie.get('userInfo'))
        this.searchForm.userOid = userInfo.userOid
        this.searchProjectList()
      },
      methods: {
        goBack: function() {
          this.$router.push('/ui/layout/home')
        },
        handleChange: function () {
          
        },
        goProjectItem: function (item) {
          this.$router.push({ path: '/ui/layout2/projectitem', query: {proOid: item.projectOid} })
        },
        searchProjectList: function () {
          console.log('searchProjectList')
          post('project/query/list', this.searchForm).then((res) => {
            console.log(res)
            if(res.retCode === this.GLOBAL.SUCCESS) {
              this.projectList = res.result.list
            } else {
              this.$message.error(res.retMsg)
            }
          })
        }
      }
    }
</script>

<style scoped>
  #pro-list-project-header .el-divider--horizontal {
    margin: 15px 0;
  }

  .pro-list-card-item {
    margin-bottom: 10px;
    padding: 0;
  }


</style>



