ight: 10px;\">\n                  <p>{{item.projectDesc?item.projectDesc: '没有描述信息!'}}</p>\n                </div>\n                <div style=\"margin-top: 10px;font-size: 10px\">\n                  <span><i class=\"el-icon-time\"></i></span>\n                  <span>{{item.createDate}}</span>\n                  <span style=\"margin-left: 20px;color: #2c3e50;\"><i class=\"el-icon-tickets\"></i>文件个数</span><span>:</span>\n                  <span style=\"color: #A8A8A8;\">{{item.fileCount}}</span>\n                  <span style=\"float: right;color: #3a8ee6;cursor: pointer;\">查看详情</span>\n                </div>\n              </div>\n            </el-card>\n          </div>\n        </div>\n      </div>\n    </div>\n</template>\n\n<script>\n  import {post} from 'api/post'\n\n    export default {\n      name: \"projectList\",\n      data() {\n        return {\n          searchForm: {\n            userOid: '',\n            projectName: ''\n          },\n          activeNames: ['1'],\n          projectList: [\n            {\n              projectOid: '1001',\n              projectName: 'T-PN1001',\n              projectDesc: 'It is the test1 Project !',\n              fileCount: 2,\n              createDate: '2019-02-12'\n            },\n            {\n              projectOid: '1002',\n              projectName: 'Test-1002',\n              projectDesc: 'It is the test2 Project ! It Create for test file pro-2019-20.jpg.',\n              fileCount: 1,\n              createDate: '2019-05-21'\n            },\n            {\n              projectOid: '1003',\n              projectName: 'ProN-Test1003',\n              projectDesc: '',\n              fileCount: 2,\n              createDate: '2019-02-12'\n            }\n          ]\n        }\n      },\n      created() {\n        let userInfo = JSON.parse(this.$cookie.get('userInfo'))\n        this.searchForm.userOid = userInfo.userOid\n        this.searchProjectList()\n      },\n      methods: {\n        goBack: function() {\n          this.$router.push('/ui/layout/home')\n        },\n        handleChange: function () {\n          \n        },\n        goProjectItem: function (item) {\n          this.$router.push({ path: '/ui/layout2/projectitem', query: {proOid: item.projectOid} })\n        },\n        searchProjectList: function () {\n          console.log('searchProjectList')\n          post('project/query/list', this.searchForm).then((res) => {\n            console.log(res)\n            if(res.retCode === this.GLOBAL.SUCCESS) {\n              this.projectList = res.result.list\n            } else {\n              this.$message.error(res.retMsg)\n            }\n          })\n        }\n      }\n    }\n</script>\n\n<style scoped>\n  #pro-list-project-header .el-divider--horizontal {\n    margin: 15px 0;\n  }\n\n  .pro-list-card-item {\n    margin-bottom: 10px;\n    padding: 0;\n  }\n\n\n</style>\n"],"sourceRoot":""}]);

