<template>
    <div>
      <div id="pro-item-project-header" style="margin-top: 15px;">
        <el-page-header style="margin-left: 6px;" @back="goBack" content="工程记录">
        </el-page-header>
        <el-divider></el-divider>
      </div>
      <div>
        <div style="margin-top: 10px;margin-left: 20px;">
          <div>
            <span class="common-label">工程名称</span>
            <span>:</span>
            <span style="margin-left: 10px;">{{form.projectName}}</span>
          </div>
          <div style="margin-top: 15px">
            <span class="common-label">工程描述</span>
            <span>:</span>
            <span style="margin-left: 10px;">{{form.projectDesc}}</span>
          </div>
        </div>
        <div style="width: 90%;margin: 20px auto;">
          <div style="margin-bottom: 13px;">
            <span class="common-label">工程文件</span>
          </div>
          <el-form ref="form" :model="form" :rules="rules2" class="demo-ruleForm" inline-message>
            <el-card class="box-card" style="margin-bottom: 10px;" body-style="padding: 0;" v-for="item in form.proFileList">
              <div style="font-size: 13px;background-color: #D2CEA5; padding: 5px;">
                <span class="common-label">文件名</span>
                <span>:</span>
                <span>{{item.filename}}</span>
                <span style="color: #3a8ee6;cursor: pointer;float: right;font-size: 12px;" @click="useRecommendLine(item)">使用推荐行</span>
              </div>
              <div style="margin-left: 7px;">
                <el-form-item prop="lineNum" style="margin-bottom: 0;">
                  <template slot="label">
                    <span>选取行</span>
                    <span>:</span>
                  </template>
                  <el-input size="mini" v-model="item.lineNum" maxlength="10" style="width:26%;font-size: 12px;"></el-input>
                </el-form-item>

                <el-form-item prop="calParam" style="margin-bottom: 0;">
                  <template slot="label">
                    <span>温度/湿度参数</span>
                    <span>:</span>
                  </template>
                  <el-input size="mini" v-model="item.calParam" maxlength="10" style="width: 26%;font-size: 12px;"></el-input>
                </el-form-item>
              </div>

              <div style="font-size: 12px;margin: 7px 7px;color: #3a8ee6;">
                <div style="display: inline-block;" @click="showSourceFile(item)">
                  <i class="el-icon-view"></i><span>查看原图</span>
                </div>
                <div style="display: inline-block;margin-left: 50px;" @click="showGrayscale(item)">
                  <i class="el-icon-picture"></i><span>查看灰度图</span>
                </div>
              </div>
            </el-card>
          </el-form>
        </div>

        <div style="text-align: center;margin-bottom: 30px;">
          <el-button type="primary" size="medium" @click="doAnalysis">分析</el-button>
        </div>
      </div>

      <el-dialog
        :title="sourceFileName"
        :visible.sync="sourceFileShow"
        width="80%"
        center>
        <div style="text-align: center;">
          <el-image
            style="width: 80%;"
            :src="sourceFileUrl"
            fit="contain"></el-image>
        </div>
      </el-dialog>

      <el-dialog
        :title="grayscaleFileName"
        :visible.sync="grayscaleShow"
        width="80%"
        center>
        <div style="text-align: center;">
          <el-image
            style="width: 80%;"
            :src="grayscaleFileUrl"
            fit="contain"></el-image>
        </div>
      </el-dialog>
    </div>
</template>

<script>
  import testFile1 from '../../assets/img/test/20.jpg'
  import {post} from 'api/post'

    export default {
      name: "projectItem",
      data() {
        var verifyNumber =  (rule, value, callback) => {
          let reg = /^[0-9]*$/
          if (!value) {
            callback(new Error('请输入数字!'))
          } else if(!reg.test(value)){
            callback(new Error('请输入数字!'))
          } else {
            callback()
          }
        }

        return {
          sourceFileShow: false,
          sourceFileName: '',
          sourceFileUrl: '',
          grayscaleShow: false,
          grayscaleFileName: '',
          grayscaleFileUrl: '',
          rules2: {
            // lineNum: [
            //   { required: true, message: '请输入行号!'},
            //   {validator: verifyNumber, trigger: 'blur'}
            // ],
            // calParam: [
            //   { required: true, message: '请输入参数!'},
            //   {validator: verifyNumber, trigger: 'blur'}
            // ]
          },
          searchForm: {
            projectOid: ''
          },
          form: {
            projectOid: '',
            projectName: 'Test-1001PN',
            projectDesc: 'It is the test2 Project ! It Create for test file pro-2019-20.jpg.And this project only test system! It create By Test Admin!',
            proFileList: [
              {
                fileOid: '1020',
                filename: 'Test1-20.jpg',
                filePath: testFile1,
                grayscale: testFile1,
                lineNum: '',
                calParam: '',
                recommendLine: 0
              },
              {
                fileOid: '1060',
                fileName: 'Test1-60.jpg',
                filePath: testFile1,
                grayscale: testFile1,
                lineNum: '',
                calParam: '',
                recommendLine: 0
              }
            ]
          }
        }
      },
      created() {
        if(!this.$route.query.proOid) {
          this.$router.push('/ui/layout2/projectlist')
        }

        this.searchForm.projectOid = this.$route.query.proOid
        this.form.projectOid = this.$route.query.proOid
        this.queryProjectInfo()
      },
      methods: {
        goBack: function () {
          this.$router.push('/ui/layout2/projectlist')
        },
        queryProjectInfo: function() {
          post('project/query/info', {projectOid: this.searchForm.projectOid}).then((res) => {
            console.log(res)
            if(res.retCode === this.GLOBAL.SUCCESS) {
              this.form = res.result
            } else {
              this.$message.error(res.retMsg)
            }
          })
        },
        useRecommendLine: function(item) {
          item.lineNum = item.recommendLine
        },
        showSourceFile: function (item) {
          this.sourceFileShow = true
          this.sourceFileName = item.fileName
          this.sourceFileUrl = item.filePath
        },
        showGrayscale: function (item) {
          this.grayscaleShow = true
          this.grayscaleFileName = item.fileName
          this.grayscaleFileUrl = item.grayscale
        },
        doAnalysis: function () {
          // 保存参数 生成线性图 保存生成数据

          //跳转页面
          this.$router.push({ path: '/ui/layout2/analysisres', query: {proOid: this.searchForm.projectOid} })
        }
      }
    }
</script>

<style scoped>
  #pro-item-project-header .el-divider--horizontal {
    margin: 15px 0;
  }
</style>



