<template>
    <div>
      <div style="background-color: #f3f3f3;border-bottom: 1px solid #f3f3f3;">
        <div style="margin: 0 20px;font-size: 22px;color: rgb(113, 157, 204);padding: 10px 0px;"><span>设置</span></div>
      </div>
      <div id="setting-body">
        <div style="margin-top: 0;">
          <el-collapse v-model="activeNames" @change="handleChange" style="margin-left: 20px;">
            <el-collapse-item title="" name="1" style="font-size: 16px;">
              <template slot="title"><span style="font-size: 16px;">模式</span></template>
              <div style="margin-left: 10px;">
                <span>开启精简模式</span>
                <el-switch
                  v-model="settingForm.isSimpleMode"
                  active-color="#13ce66"
                  inactive-color="rgb(158, 152, 152)" style="position: absolute;right: 15px;"
                  disabled>
                </el-switch>
              </div>
            </el-collapse-item>
            <el-collapse-item title="" name="2" style="font-size: 16px;">
              <template slot="title"><span style="font-size: 16px;">下载</span></template>
              <div style="margin-left: 10px;">
                <span>下载前询问</span>
                <el-switch
                  v-model="settingForm.isAutoDownload"
                  active-color="#13ce66"
                  inactive-color="rgb(158, 152, 152)" style="position: absolute;right: 15px;"
                  disabled>
                </el-switch>
              </div>
              <div style="margin-left: 10px;margin-top: 9px;">
                <span>默认下载地址</span>
                <span style="position: relative;left: 50px;color: rgb(179, 179, 179);">{{settingForm.downloadAddress}}</span>
                <el-link type="primary" style="position: absolute;right: 22px;" disabled>修改</el-link>
              </div>
            </el-collapse-item>
            <el-collapse-item title="" name="3" style="font-size: 16px;">
              <template slot="title"><span style="font-size: 16px;">配置参数</span></template>
              <div style="margin-left: 10px;">
                <span>单工程最大上传文件数</span><span>: </span>
                <span style="color: rgb(179, 179, 179);">{{settingForm.maxLimitUpload}}</span>
                <el-link type="primary" style="position: absolute;right: 22px;" disabled>修改</el-link>
              </div>
              <div style="margin-left: 10px;margin-top: 9px;">
                <span>X轴放大倍数</span><span>: </span>
                <span style="color: rgb(179, 179, 179);">{{settingForm.multipleX}}</span>
                <el-link type="primary" style="position: absolute;right: 22px;">修改</el-link>
              </div>
              <div style="margin-left: 10px;margin-top: 9px;">
                <span>单位</span>
                <el-radio-group style="position: absolute;right: 22px;" size="mini" v-model="settingForm.calParamUnit">
                  <el-radio-button label="T">温度</el-radio-button>
                  <el-radio-button label="H">湿度</el-radio-button>
                </el-radio-group>
              </div>
            </el-collapse-item>
          </el-collapse>
        </div>
      </div>
    </div>
</template>

<script>
    export default {
        name: "setting",
        data() {
          return {
            activeNames: [],
            settingForm: {
              isSimpleMode: true,
              isAutoDownload: true,
              downloadAddress: '/MIA/Download',
              multipleX: 6,
              maxLimitUpload: 2,
              calParamUnit: 'T'
            }
          }
        },
        created() {

        },
        methods: {
          handleChange:function() {

          }
        }
    }
</script>

<style scoped>
  #collapse-item1>.el-collapse-item__header {
    font-size: 16px !important;
  }
</style>



